      </span>
      </v-btn>
       <v-btn 
        class="ml-10 mt-9"
      ><h1>{{this.wedName}}</h1></v-btn>

       <v-container  style="width:80%" class="mt-12">
      <v-layout justify-center  row wrap>
        <v-flex
         class="mx-auto"
         v-for="item in this.images"
         :key="item"
        >
    <v-hover v-slot="{ hover }">

      <v-img
        width="244"
        :src="`http://localhost:3000/`+item"
        :lazy-src="`http://localhost:3000/`+item"
        aspect-ratio="1"
        class="grey lighten-2 mb-auto mx-auto mt-9"
      >
          <template v-slot:placeholder>
            <v-row class="fill-height ma-0" align="center" justify="center">
              <v-progress-circular
                indeterminate
                color="grey lighten-5"
              ></v-progress-circular>
            </v-row>
          </template>
           <v-expand-transition>
          <div
            v-if="hover"
            class="d-flex transition-fast-in-fast-out black  v-card--reveal text-h2 white--text"
            style="height: 100%;"
          >
           <v-btn
           id="op1"
      class="mr-2"
