        width="500"
      >
        <v-card>
          <v-card-title>
            Brisanje fotografije
          </v-card-title>
          <v-card-text>
            Da li ste sigurni da elite da obriete ovu fotografiju?
          </v-card-text>
          <v-divider></v-divider>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              color="info"
              text
              @click="dialog=false"
            >
