            </v-btn>
            <v-btn
              color="green"
              text
              :loading="loading"
              @click="dodajFotografiju()"
            >
            <v-icon >mdi-content-save</v-icon>

              Dodaj
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <v-dialog
        v-model="dialogDeleteAll"
