          </v-card-title>
          <v-card-text>
            <span class="er">
            Da li ste sigurni da elite da obriete sve fotografije?<br>
            Povratak nee biti mogu!!!
            </span>
          </v-card-text>
          <v-divider></v-divider>
          <v-card-actions>
            <v-spacer></v-spacer>
