              color="info"
              text
              @click="dialogDeleteAll=false"
            >
            <v-icon >mdi-close-circle</v-icon>
              Odustani
            </v-btn>
            <v-btn
              color="red"
              text
              @click="deleteAllFotografija(id)"
            >
            <v-icon>mdi-delete</v-icon>
              Obrii sve
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
