</template>


<script>
import apiPost from "../../services/service"
// @ is an alias to /src

  export default {
    data: () => ({
      search: "",
      dialog: false,
      dialogEdit: false,
      dialogAdd: false,
      dialogDeleteAll: false,
      itemsPerPage: 4,
      images: [],
      image:[],
      imageAdd:[],
      tab: null,
      loading: false,
      id:null,
      item:null,
      wedName: null,
      
    
    }),

    async mounted () {
      
      this.id = await  this.$route.params.id
      try {
        // Simple GET request using axios
       await apiPost.getCard(this.id).then(async response => {
          this.images = await response.data.images
          this.wedName = await  response.data.name
       })
       
      } catch (error) {
        console.log(error.message)
      }
    },

    methods: {
     async deleteFotografija(item){
      try {
        let formData = {
          "image": item,
          "id": this.id
        }
        await apiPost.deleteImage(formData)
       .then(response => {
            console.log(response.data)
        })
      await apiPost.getCard(this.id).then(async response => {
          this.images = await response.data.images
       })
       this.dialog = false;
     
      } catch (error) {
        console.log(error.message)
        
      }
      
       
       
     },
     async deleteAllFotografija(id){
      try {
        await apiPost.deleteImageAll(id).then(response => {
        console.log(response.data)
       })
       await apiPost.getCard(this.id)
          .then(async response => {
             this.images   = await response.data.images
             
          })
      } catch (error) {
        console.log(error.message)
        
      }
      this.dialogDeleteAll = false;
     },
     async editFotografija(id){
      try {
        let formData = new FormData();
           if (this.image) {
          formData.append("image[]", this.image);
          console.log(formData)
          console.log(this.image)
          await apiPost.editImage(id,this.item,formData)
          .then(response => {
            console.log(response.data)
          })
          await apiPost.getCard(this.id).then(async response => {
          this.images = await response.data.images
          this.dialogEdit = false;

       })
          this.image = []
          this.item = null
      }
      } catch (error) {
        console.log(error.message)
      }       
     },
      deleteItem(item) {
      this.item = item
      this.dialog = true
    },
     editItem(id, item) {

      this.dialogEdit = true
      this.item = item
    },
  
    async dodajFotografiju(){
      
       this.loading = true;
       let formData = new FormData()
       try {
          let files = this.imageAdd
          let filesLength = files.length
          for(var i = 0; i < filesLength; i++){
            formData.append("image[]", files[i])
          
          }
            formData.append("id", this.id)

          if (this.imageAdd) {
          await apiPost.addImages(formData)
          .then(async response => {
            console.log(await response.data)
          })
          await apiPost.getCard(this.id)
          .then(async response => {
             this.images   = await response.data.images
             
          })
          this.dialogAdd = false;
          this.imageAdd = []
          this.loading = false;
       }
        
       } catch (error) {
        console.log(error.message)
       }
         
     }
       ,
     reserve () {
        this.loading = true
        setTimeout(() => (this.loading = false), 2000)
      },
     

     
    },
  }
</script>




